
.caption {
  text-align: center;
  height: 100vh;
  display: flex;
  place-content: center;
  align-items: center;
  background: url("https://img.freepik.com/free-vector/happy-birthday-golden-silver-balloons-with-confetti_1017-29992.jpg?w=740&t=st=1663579498~exp=1663580098~hmac=aa362467f8fed57e240d64a7699d343c5964412f72ea193f1ebad63cf330f3ce") no-repeat;
  background-size: cover;
  color: rgb(0, 0, 0)02);
}
.card {
  backdrop-filter: blur(8px) saturate(180%);
  -webkit-backdrop-filter: blur(8px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.39);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}
